@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700&display=swap);
.home-header{
    height: 10vh;
    max-height: 80px;
    min-height:60px;
    display: flex;
    align-items: center;
    width:90%;
    margin:auto;
  
}
.home-header span{
    font-size: calc(1.4rem  + 1vw);
}
.home-texts{
    margin: 5vh auto;
    width:90%;
}
.section-1{
}
.CTA{
    width:90%;
    margin: auto;
    display: flex;
    height:20vh;
    flex-direction: column;
    align-items: center;
    grid-gap:1rem;
    gap:1rem;
}
.CTA a{
    text-decoration:none;
    padding:10px;
    width:80%;
    text-align: center;
    border-radius:10px;
    font-size: 1.2rem;
    max-width: 300px;
}
.cta-border{
    border: 1px solid #2F72FF
    
}
.home-title{
    height:10vh;
    display: flex;
    align-items: center;
    font-size: calc(1.2rem + 1vw);
    font-weight: 500;
}
.home-subtitle,.CTA a{
    font-weight: 300;
}
.home-subtitle{
    max-width: 40ch;
}
.section-2{
    height:45vh;
    display: flex;
    align-items: flex-end;
    overflow: hidden;
}
.section-2 img{
    width:100%;
    object-fit: cover;
   
}
.illustration{
    display:none;
}  
@media only screen and (min-width:810px){
    .home-title{
        height:10vh;
        font-size: calc(1.2rem + 1vw);
        font-weight: 500;
    }
    .home{
        display:flex;
        height:90vh;
    }
    .section-1{
        display: flex;
        align-items: center;
        justify-content:flex-end;
        width:60%;
  
    }
    .section-2{
        height:45vh;
    
        display: flex;
        align-items: center;
        height: 100vh;
        background:url(/static/media/Desktop.6958d4e3.svg);
        background-repeat: no-repeat !important;
        background-size: cover;
        width:80%;
        display: flex;
        justify-content:flex-end !important;
      
        
    }
    .home-subtitle{
        max-width: 35ch;
    }
    .CTA{
        height:10vh !important;
        flex-direction: row;
    }

    .home-header{
        height: 10vh;
        max-height: 80px;
        min-height:60px;
        display: flex;
        align-items: center;
        background:white;
        width:40%;
        position: absolute;
    }
    .home-header span{
        margin-left: 3rem;
    }
    
    .section-2 img{
        width:100%;
        object-fit: cover;
    
    }
    .illustration{
        width:50% !important;
        margin-right: 3rem;

    }
}
@media only screen and (min-width:1285px){
   .illustration{
    display:block;
   }   
}


.sign-in,.sign-up{
    width:100%;
    margin:10vh auto;
    max-width:700px;

}
.page-title{
    height: 10vh;
}
.form-title{
    height: 15vh;

    display:flex;
    align-items:center;
}
.form-title span{
    font-size: calc(1.2rem + 1vw);
}
.form-wrapper{
    width:80%;
    margin:auto;
    height: 10vh;
    
}
.form-form{

    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.input-wrapper{
    display: flex;
    flex-direction: column;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    
}
.input-wrapper input , .input-wrapper select{
    width: 100%;
    height: 2.5rem;
    border-radius: 5px;
    border: none;
  
}
.btn-wrapper{
    height:10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
 .btn{
    height: 6vh;
    width:calc(15rem + 1vw);
    border-radius:10px;
    border:none;
    font-size: calc(0.8rem + 1vw)
}

.no-account a, .have-account a{
    text-decoration: none;
    font-weight: 300;
    color: #c5c6c7;
}
.input-wrapper textarea{
    resize: none;
    height: 8vh;
    border: none;
}
.divider{
    width:100%;
    height: 3vh;
}
.input-wrapper input:focus,.input-wrapper select:focus,.input-wrapper textarea:focus{
    outline:2px solid #6BD382;
    outline-offset: -2px;
}
.input-wrapper .error-message{
    color:#db5248;
    font-weight:200;
    font-size: 0.8rem;
}
.error-message{
    display:none;
}
.steps{
    height:10vh;
    width:100%;

    display:flex;
    justify-content:center;
    align-items:center;
    border-radius: 10px;
}
.steps > *{
    width:30%;
}

.steps li{
    height: 8vh;
   display:flex;
   justify-content: center;
   align-items:center;
   list-style: none;
   font-size:calc(0.6rem + 0.2vw);
   background-color:#f7f7f5;
   border-left: 1px solid #cdcfd1

}
.active{
    background-color:#000000 !important;
    color:#f7f7f5;
    border-radius: 5px;
}
.input-code{
    font-size:2rem;
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
.fixed-pos{
    position:fixed;
    z-index:2;
   top:15rem;
    height: 55vh;
    border-radius: 10px;
    left: 50%;
    transform:translate(-50%);
    max-width:420px;
    -webkit-filter: drop-shadow(1px 2px 3px #bbbdbf);
            filter: drop-shadow(1px 2px 3px #bbbdbf);
}

